import BaseApiService from "../BaseApiService";

const DEFAULT_PATH = "/custom/api";

class FeeService extends BaseApiService {
  getDdeFeeList = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/loan/fee/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  getDdeReceiptList = (start = 0, limit = 10, loanUid: string) => {
    const url = `${DEFAULT_PATH}/login/${loanUid}/receipt?start=${start}&limit=${limit}&isActive=true`;
    return this.makeGetRequestWithAuth(url);
  };

  getBalance = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/balance/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  createDdeReceipt = ( payload: any) => {
    const endPoint = DEFAULT_PATH + `/login/dde/receipt`;
    return this.makePostRequestWithAuth(endPoint,payload);
  };

  updateDdeReceipt = ( payload: any) => {
    const endPoint = DEFAULT_PATH + `/login/dde/receipt`;
    return this.makePutRequestWithAuth(endPoint,payload);
  };

  deleteDdeReceipt = ( uid: any) => {
    const endPoint = DEFAULT_PATH + `/login/dde/receipt/inactivate/${uid}`;
    return this.makePutRequestWithAuth(endPoint);
  };

  getDdeUpiQrAndLink = (upiId:string) => {
    const endPoint = DEFAULT_PATH + `/login/dde/receipt/upi?upiId=${upiId}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  // Api's for Fee collections
 
  createFeeCollectionReceipt = ( payload: any) => {
    const endPoint = DEFAULT_PATH + `/receipt/manage`;
    return this.makePostRequestWithAuth(endPoint,payload);
  };

  updateFeeCollectionReceipt = ( payload: any) => {
    const endPoint = DEFAULT_PATH + `/receipt/manage`;
    return this.makePutRequestWithAuth(endPoint,payload);
  };

  updateFeeCollectionChangeStatus = ( payload: any) => {
    const endPoint = DEFAULT_PATH + `/deposit/receipt/change/status`;
    return this.makePutRequestWithAuth(endPoint,payload);
  };

  getFeeCollectionReceipt = (start = 0, limit = 10,search="") => {
    const endPoint = DEFAULT_PATH + `/receipt/manage/search?start=${start}&limit=${limit}&isActive=true${search}`;
     console.log("receipt " , search);
    return this.makeGetRequestWithAuth(endPoint); 
  };
  
  getLoans = (start = 0, limit = 10,search="") => {
    const endPoint = DEFAULT_PATH + `/loan/search?start=${start}&limit=${limit}&isActive=true${search}`;
     console.log("receipt " , search);
    return this.makeGetRequestWithAuth(endPoint); 
  };

  feeCollectionSharePaymentDetailsByEmail = ( bankAccountUid: string) => {
    const endPoint = DEFAULT_PATH + `/receipt/manage/sent/email/${bankAccountUid}`;
    return this.makePostRequestWithAuth(endPoint);
  };

  feeCollectionSearchReceipt = (start :any, limit :any, search: string) => {
    const endPoint = DEFAULT_PATH + `/receipt/search?start=${start}&limit=${limit}&isActive=true&${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  feeCollectionReceiptDetails = (uid:string) => {
    const endPoint = DEFAULT_PATH + `/receipt/manage/detail/${uid}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  createFeeCollectionDeposit = (payload:any) => {
    const endPoint = DEFAULT_PATH + `/manage/deposit`;
    return this.makePostRequestWithAuth(endPoint,payload);
  };

  getOneFeeCollectionDeposit = (depositUid:any) => {
    const endPoint = DEFAULT_PATH + `/manage/deposit/details/${depositUid}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  getFeeCollectionDepositList = (start=0,limit=10,search:string) => {
    const endPoint = DEFAULT_PATH + `/manage/deposit/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  getFeeCollectionReconciliationList = (start=0,limit=10,search:string) => {
    const endPoint = DEFAULT_PATH + `/deposit/receipt/search?start=${start}&limit=${limit}&isActive=true${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  getUserList = (start=0,limit=10,search:string) => {
    const endPoint = DEFAULT_PATH + `/user/profile/search?start=${start}&limit=${limit}&isActive=true${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}

export default new FeeService();
